/* Timeline.css */
.timeline {
    position: relative;
    width: 100%;
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .line {
    position: absolute;
    width: 4px;
    background-color: #ccc;
    top: 0;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  
  .timeline-event {
    position: relative;
    width: 100%;
    padding: 10px 20px;
    box-sizing: border-box;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px 0;
  }
  
  .timeline-event.left {
    flex-direction: row-reverse;
  }
  
  .timeline-event.right {
    flex-direction: row;
  }
  
  .dot {
    width: 12px;
    height: 12px;
    background-color: #ccc;
    border-radius: 50%;
    margin: 0 10px;
  }
  
  .event-details {
    display: none;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    background-color: #f9f9f9;
    border: 1px solid #ccc;
    padding: 10px;
    border-radius: 5px;
    z-index: 1;
  }
  
  .timeline-event:hover .event-details {
    display: block;
  }
  
  .timeline-event.left .event-details {
    left: auto;
    right: 50%;
    transform: translateX(50%);
  }
  
  .timeline-event.right .event-details {
    left: 50%;
    transform: translateX(-50%);
  }
  